<template>
    <div id="floatdiv">
        <p>주문서 작성중</p>
        <p />
        <b-list-group style="max-width: 300px;">
            <b-list-group-item class="d-flex align-items-center" v-for="(item, key, idx) in vuex_menu" :key="idx">
                <template v-if="item">
                    <b-avatar variant="info" :src="item.img" class="mr-3"></b-avatar>
                    <span class="mr-auto">{{ item.name }}</span
                    >&nbsp;
                    <b-badge>{{ item.cnt }}</b-badge>
                </template>
            </b-list-group-item>
            <b-button variant="primary" @click="order">주문</b-button>
        </b-list-group>
    </div>
</template>
<style scoped>
#floatdiv {
    position: fixed;
    _position: absolute;
    _zindex: -1;
    overflow: hidden;
    right: 0px;
    top: 60px;
    background-color: transparent;
    margin: 0;
    padding: 0;
}
.hidden {
    display: none;
}
</style>
<script>
import consts from "@/consts";
export default {
    computed: {
        loginId() {
            return this.$store.state.loginId;
        },
        vuex_menu() {
            return this.$store.getters.orders;
        },
    },
    watch: {
        vuex_menu: {
            deep: true,
            handler: function(newval) {
                console.log("menuboard", newval);
                this.vuex_menu;
            },
        },
    },
    methods: {
        order() {
            let details = [];
            for (let key in this.vuex_menu) {
                let item = this.vuex_menu[key];
                details.push({ productId: key, quantity: item.cnt });
            }

            let myOrder = { userId: this.loginId, orderTable: "웹주문", orderTime: new Date(), completed: "N", details };
            consts
                .axios({
                    method: "post",
                    url: "/order",
                    data: myOrder,
                })
                .then((res) => {
                    console.log(res);
                    if (res.data) {
                        alert("주문 되었습니다.");
                        this.$store.commit("clearMenu");
                    }
                });
        },
    },
};
</script>
