<template>
    <b-container id="app">
        <div>
            <b-navbar toggleable="lg" type="dark" variant="info">
                <b-navbar-brand
                    ><router-link to="/">
                        <b-img :src="img_url + '/logo.png'" fluid alt="Responsive image" class="logo"></b-img>
                        SSAFY Cafe</router-link
                    >
                </b-navbar-brand>

                <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

                <b-collapse id="nav-collapse" is-nav>
                    <!-- Right aligned nav items -->
                    <b-navbar-nav class="ml-auto" v-if="loginId == ''">
                        <b-nav-item size="sm" class="my-2 my-sm-0" v-b-modal.login-modal>로그인</b-nav-item>
                        <b-nav-item size="sm" class="my-2 my-sm-0"><router-link to="/join">회원가입</router-link></b-nav-item>
                    </b-navbar-nav>
                    <b-navbar-nav class="ml-auto" v-if="loginId != ''">
                        <b-nav-item size="sm" class="my-2 my-sm-0">
                            <router-link to="/user">{{ userNick }}</router-link>
                        </b-nav-item>
                        <b-nav-item size="sm" class="my-2 my-sm-0">
                            <a href="#" @click="logout">로그아웃</a>
                        </b-nav-item>
                    </b-navbar-nav>
                </b-collapse>
            </b-navbar>
        </div>
        <hr />
        <router-view />
        <hr />
        <b-row id="footer" align-h="end">
            <b-nav>
                <b-nav-item>카페소개</b-nav-item>
                <b-nav-item>개인정보처리방침</b-nav-item>
                <b-nav-item>이용약관</b-nav-item>
                <b-nav-item>오시는길</b-nav-item>
            </b-nav>
        </b-row>

        <b-modal id="login-modal" hide-footer title="로그인">
            <div>
                <b-form>
                    <b-form-group id="input-group-1" label="사용자ID:" label-for="input-1">
                        <b-form-input id="input-1" v-model="user.id" type="text" placeholder="아이디를 입력하세요." required></b-form-input>
                    </b-form-group>

                    <b-form-group id="input-group-2" label="비밀번호" label-for="input-2">
                        <b-form-input id="input-2" v-model="user.pass" placeholder="비밀번호를 입력하세요." required></b-form-input>
                    </b-form-group>
                    <b-row align-h="end">
                        <b-button-group>
                            <b-button variant="success" @click="login">로그인</b-button>
                            <b-button variant="info" type="reset" @click="reset">리셋</b-button>
                            &nbsp;&nbsp;&nbsp;&nbsp;
                        </b-button-group>
                    </b-row>
                </b-form>
            </div>
        </b-modal>
    </b-container>
</template>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
}

#nav {
    padding: 30px;
}

#nav a {
    font-weight: bold;
    color: #2c3e50;
}

#nav a.router-link-exact-active {
    color: #42b983;
}

.logo {
    width: 3em;
    height: 3em;
}
</style>

<script>
import consts from "@/consts";
export default {
    data() {
        return {
            img_url: consts.img_url,
            user: {
                id: "",
                pass: "",
            },
            show: true,
        };
    },
    computed: {
        loginId() {
            return this.$store.state.loginId;
        },
        userNick() {
            return this.$store.state.userNick;
        },
    },
    methods: {
        login() {
            this.$store.dispatch("login", this.user);
            this.$bvModal.hide("login-modal");
            this.reset();
            this.$router
                .push({
                    path: "/",
                })
                .catch(() => {});
        },
        reset() {
            this.user.id = "";
            this.user.pass = "";
        },
        logout() {
            this.$store.commit("logout");
            // vuex-persistedstate 플러그인에 의해 저장된 정보 삭제
            localStorage.removeItem("vuex");
            if (this.$route.path != "/") {
                this.$router.push({
                    path: "/",
                });
            }
        },
    },
};
</script>
