import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);
import consts from "@/consts";
import createPersistedState from "vuex-persistedstate";

export default new Vuex.Store({
    plugins: [createPersistedState()],
    state: {
        loginId: "",
        userNick: "",
        order_menu: {},
        order_completed: false,
    },
    getters: {
        orders(state) {
            return state.order_menu;
        },
        orderCompleted(state) {
            return state.order_completed;
        },
    },
    mutations: {
        logout(state) {
            state.order_completed = true;
            state.order_menu = {};
            state.loginId = "";
        },
        login(state, payload) {
            state.loginId = payload.id;
            state.userNick = payload.name;
        },
        setMenu(state, payload) {
            state.order_completed = false;
            if (payload.cnt == 0) {
                Vue.delete(state.order_menu, payload.id);
            } else {
                //state.order_menu[payload.id] = { cnt: payload.cnt, img: payload.img, name: payload.name };
                Vue.set(state.order_menu, payload.id, { cnt: payload.cnt, img: payload.img, name: payload.name });
            }
        },
        clearMenu(state) {
            state.order_completed = true;
            state.order_menu = {};
        },
    },
    actions: {
        login(store, payload) {
            consts
                .axios({
                    url: "/user/login",
                    method: "post",
                    data: payload,
                })
                .then((res) => {
                    if (res.data) {
                        store.commit("login", res.data);
                    } else {
                        alert("로그인 실패입니다. 아이디와 비밀번호를 확인하세요.");
                    }
                })
                .catch(() => alert("로그인 처리 실패"));
        },
    },
    modules: {},
});
