<template>
    <div>
        <b-container class="bv-example-row">
            <menu-by-type variant="primary" title="커피" desc1="뛰면서 즐기는 커피 한잔의 여유" desc2="갓 볶은 아라비카산 원두만 고집합니다." :prods="coffees" />
            <menu-by-type variant="success" title="차" desc1="차분히 앉아서 차향을 느껴보세요." desc2="어린 잎만 정성들여 우려냅니다." :prods="teas" />
            <menu-by-type variant="danger" title="쿠키" desc1="달달한 쿠키도 있어요." desc2="맛있게 먹으면 0칼로리" :prods="cookies" />
        </b-container>
        <menu-board v-if="Object.keys(vuex_menu).length"></menu-board>
    </div>
</template>
<style scoped></style>

<script>
import consts from "@/consts";
import MenuByType from "./MenuByType.vue";
import MenuBoard from "./MenuBoard.vue";
export default {
    data() {
        return {
            products: [],
            coffees: [],
            teas: [],
            cookies: [],
            order_menu: {},
        };
    },
    computed: {
        vuex_menu() {
            return this.$store.getters.orders;
        },
    },
    watch: {
        vuex_menu: {
            deep: true,
            handler: function(newval) {
                console.log("home.vue, ", newval, this.vuex_menu);
            },
        },
    },
    created: function() {
        consts
            .axios({
                method: "get",
                url: "/product",
            })
            .then((res) => {
                //console.log(res);
                this.products = res.data;
                for (let prod of res.data) {
                    if (prod.type == "coffee") {
                        this.coffees.push(prod);
                    } else if (prod.type == "tea") {
                        this.teas.push(prod);
                    } else {
                        this.cookies.push(prod);
                    }
                }
            })
            .catch();
    },
    methods: {},
    components: {
        MenuByType,
        MenuBoard,
    },
};
</script>
