<template>
    <div>
        <b-card :title="item.name" :img-src="img_url + '/menu/' + item.img" img-alt="제품" img-top tag="article" style="max-width: 20rem;" class="mb-2">
            <b-card-text> {{ item.price }} 원 </b-card-text>
            <div v-if="loginId != ''">
                <b-button variant="primary" class="mbtn">
                    <b-badge variant="light"> {{ cnt }}</b-badge>
                </b-button>
                <b-button @click="plus" variant="success" class="mbtn">+</b-button>
                <b-button @click="minus" variant="secondary" class="mbtn">-</b-button>
                <b-button @click="comment(item.id)" variant="warning" class="mbtn"><b-icon icon="chat-text-fill" font-scale="1"></b-icon></b-button>
            </div>
        </b-card>
    </div>
</template>
<style scoped>
.mbtn {
    margin: 0 3px;
}
</style>
<script>
import consts from "@/consts";
export default {
    data() {
        return {
            cnt: 0,
            img_url: "",
        };
    },
    props: {
        item: {},
    },
    computed: {
        loginId() {
            return this.$store.state.loginId;
        },
        orderCompleted() {
            return this.$store.getters.orderCompleted;
        },
    },
    watch: {
        orderCompleted(val) {
            this.cnt = val == true ? 0 : this.cnt;
        },
    },
    created() {
        this.img_url = consts.img_url;
    },
    methods: {
        plus() {
            this.cnt++;
            this.update();
        },
        minus() {
            if (this.cnt < 1) {
                return;
            }
            this.cnt--;
            this.update();
        },
        comment() {
            this.$router.push({
                path: "/comment/" + this.item.id,
            });
        },
        update() {
            this.$store.commit("setMenu", { id: this.item.id, name: this.item.name, img: this.img_url + "/menu/" + this.item.img, cnt: this.cnt, price: this.item.price * this.cnt });
        },
    },
};
</script>
